import React from "react";
import ReactDOM from "react-dom";
import "./bulma.css";
import "./index.css";
var name = "";
var temp = "";
var weath = "";
var time = "";
var weatherDom = "";

class Weather extends React.Component {
  constructor(props) {
    super(props);

    this.location = "Tokyo";
    this.state = {
      title: "Weather App",
      value: "Type Here",
      cityName: "",
      curTemp: "",
      curWeath: ""
    };
  }
  componentDidMount() {
    let currentComponent = this;
    fetch(
      "https://api.openweathermap.org/data/2.5/weather?q=Tokyo&units=metric&appid=610bc8db7bf77556f1e0f29e4c9344a9"
    )
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        name = myJson["name"];
        //currentComponent.setState({ cityName: name });
        temp = myJson["main"]["temp"];
        weath = myJson["weather"][0].main;
        switch (weath) {
          case "Rain":
            weatherDom = <i id="weIcon" class="fas fa-tint" />;
            break;

          case "Sunny":
            weatherDom = <i id="weIcon" class="fas fa-info-circle" />;
            break;
        }
        currentComponent.setState({ curTemp: temp });
        currentComponent.setState({ curWeath: weath });
      });
  }

  render() {
    return (
      <section class="hero is-dark">
        <div class="hero-body">
          <div class="container">
            <h1 class="title">{name}</h1>
            <p class="subtitle" />
            <p class="subtitle">
              <strong>Temperature: {this.state.curTemp} c</strong>
            </p>
            <p class="subtitle">
              <strong>{this.state.curWeath} </strong>
              {weatherDom}
            </p>
            <iframe
              class="youtube"
              width="560"
              height="315"
              src="https://www.youtube.com/embed/JYBpu1OyP0c?rel=0&amp;controls=0&amp;showinfo=0"
              frameborder="0"
              allow="autoplay; encrypted-media"
              allowfullscreen
            />
          </div>
        </div>
      </section>
    );
  }
}

ReactDOM.render(
  <div>
    <Weather />
  </div>,
  document.getElementById("root")
);



